<template>
  <div class="uc-field">
    <input :type="type" :value="value" @input="$emit('input', $event.target.value)" />
    <span v-if="label">{{ label }}</span>
  </div>
</template>

<script>
export default {
  name: 'uc-field',
  props: {
    label: {
      type: String
    },
    value: {
      type: [Number, String]
    },
    type: {
      type: String,
      default: 'text'
    }
  }
};
</script>

<style scoped>
.uc-field {
  border-bottom: 1px solid #e5e7e8;
  height: 0.95rem;
  position: relative;
}
.uc-field input {
  padding-left: 1.24rem;
  width: 4.54rem;
  height: 100%;
  font-size: 0.4rem;
}
.uc-field span {
  width: 1.23rem;
  height: 0.49rem;
  position: absolute;
  top: 0.26rem;
  left: 0;
  border-right: 1px solid #676868;
  color: #676868;
  font-size: 0.25rem;
  line-height: 0.49rem;
  text-align: center;
}
</style>
