<template>
	<div class="nav" :style="{
      background: bgColor,
      'border-width': cptBorderWidth
    }">
		<ul>
			<li @click="goback" :class="cptArrow"></li>
		</ul>
	</div>
</template>

<script>
	export default {
		props: {
			bgColor: {
				type: String,
				default: '#fff'
			},
			borderWidth: {
				type: Number,
				default: 1
			},
			arrow: {
				type: String,
				default: 'default'
			}
		},
		computed: {
			cptBorderWidth() {
				return this.borderWidth + 'px'
			},
			cptArrow() {
				return this.arrow
			}
		},
		methods: {
			goback() {
				this.$router.go(-1)
			}
		}
	}
</script>

<style scoped>
	.nav {
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background: #ffd;
		border-bottom: 1px solid #e8eaec;
		z-index: 99;
	}

	.nav ul {
		width: 6.4rem;
		height: 0.45rem;
		padding-top: 0.15rem;
		margin: 0 auto;
	}

	.nav ul li {
		width: 0.29rem;
		height: 0.31rem;
		/* background:url(./assets/img/history.png) no-repeat 0 0; */
		background-size: 100%;
		margin: 0 0 0 0.38rem;
	}

	.nav ul li.gray {
		background-image: url(./assets/history1.png)
	}

	.nav ul li.default {
		background-image: url(./assets/history.png)
	}
</style>
