<template>
	<div class="home paddingTop">

		<uc-swiper :data="banner" collectionName='banner' />

		<div class="home__list">
			<uc-cell v-for="(item,index) of home" :key="item._id" :index="index" :data="item"
				:to="`/detail/${item._id}?collectionName=home`" />
		</div>



	</div>
</template>

<script>
	import UcSwiper from '../components/uc-swiper/uc-swiper.vue'
	import UcCell from '../components/uc-cell/uc-cell.vue'
	// import axios from 'axios';
	export default {

		data() {
			return {
				banner: [],
				home: []
			}
		},
		components: {
			UcSwiper,
			UcCell
		},

		mounted() {

			this.$axios({
				// axios({
				url: '/news/home',
				params: {
					_limit: 30
				},
			}).then(
				res => this.home = res.data.data
			)

			this.$axios({
				// axios({
				url: '/news/banner',
				params: {
					_limit: 3
				},
			}).then(
				res => {
					[] || res.data.data.map((item) => {
						if (!item.banner.includes('http')) {
							item.banner = this.$baseUrl + item.banner;
						}

					})

					this.banner = res.data.data
				}
			)

		}
	}
</script>

<style scoped>
	.home__list {
		padding: 0 .2rem;
	}
</style>
