<template>
  <div class="column paddingTop">
    
    <div class="column__list">
      <uc-cell
        v-for="(item) of column"
        :key="item._id"
        :data="item"
        :to="`/detail/${item._id}?collectionName=column`"
      />
    </div>
    
    
    
  </div>
</template>

<script>
  import UcCell from '../components/uc-cell/uc-cell.vue'
  
  export default {
    
    data(){
      return {
        column:[]
      }
    },
    components:{
      UcCell
    },
    
    mounted(){
       this.$axios({
       // axios({
         url:'/news/column',
         params:{
           _limit: 10
         },
       }).then(
         res=>this.column=res.data.data
       )
    }
  }
</script>

<style scoped>
  .column__list{
    padding: 0 .2rem;
  }
</style>
