import Vue from 'vue'
import App from './layouts/App.vue'

import './assets/css/base.css'
import './assets/js/fonts.js'

Vue.config.productionTip = false

import router from './plugins/router.js'//路由的配置

import './global'//引入所有全局配置

import axios from './plugins/axios'
Vue.prototype.$axios = axios;

import {baseUrl} from './config';
Vue.prototype.$baseUrl = baseUrl; //配置接口服务器基础地址


let vm = new Vue({
  data:{
    bNav:false,
    bFoot:false,
    bLoading:false
  },
  render: h => h(App),
  router
}).$mount('#app');

export default vm;
